@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap');



::-webkit-scrollbar {
    width: 5px;
    border-radius: 30px;

}

::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.203);
}


::-webkit-scrollbar-thumb {
    background: #888;
}


::-webkit-scrollbar-thumb:hover {
    background: #555;
}


* {
    font-family: 'Roboto', sans-serif;
}



// login
.login {
    height: 100vh;
    width: 100vw;
    background-color: #FAC204;
    display: flex;
    align-items: center;
    justify-content: center;

    .loginContainer {
        height: 90%;
        width: 350px;
        padding: 20px 0px 20px 0px;
        background-color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        border-radius: 15px;
        box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;

        .appName {
            display: flex;
            align-items: center;

            .logo {
                width: 100px;
            }

            h2 {
                text-transform: uppercase;
                font-weight: bolder;
                color: #4e426d;
            }
        }

        img {
            width: 350px;
        }

        .welcome {
            display: flex;
            align-items: center;
            flex-direction: column;

            .welcomeText {
                margin: 0 10px;
                color: #4e426d;
            }

            p {
                margin: 0px;
                margin-bottom: 20px;
                font-size: 12px;
            }

            button {
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 10px 10px;
                border: none;
                background-color: #4e426d;
                border-radius: 8px;
                color: white;
                font-size: 15px;
                width: 300px;
                cursor: pointer;

                img {
                    width: 25px;
                    padding-left: 10px;
                }
            }
        }

    }
}

// details
.details {
    height: 100vh;
    width: 100vw;
    background-color: #FAC204;
    display: flex;
    align-items: center;
    justify-content: center;

    .detailsContainer {
        padding: 30px 0px;
        width: 350px;
        display: flex;
        flex-direction: column;
        gap: 15px;
        background-color: white;
        border-radius: 15px;
        box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
        align-items: center;
        justify-content: center;

        .appName {
            display: flex;
            align-items: center;

            .logo {
                width: 100px;
            }

            h2 {
                text-transform: uppercase;
                font-weight: bolder;
            }
        }

        h2 {
            color: #4e426d;
        }

        p {
            font-size: 14px;
        }

        .input {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            border-bottom: 2px solid #4e426d;
            color: #4e426d;
            width: 80%;
            font-size: 30px;

            input {
                font-size: 16px;
                padding: 15px;
                border: none;
                outline: none;
            }

            &:focus-within {
                border-bottom: 2px solid #FAC204;
                color: #FAC204;
            }
        }

        label {
            display: flex;
            align-items: center;

            img {
                width: 30px;
            }

            span {
                color: #4e426d;
                padding: 10px;
            }

            cursor: pointer;
        }

        #imgpreview {
            display: none;
            border-radius: 20px;
            img {
                height: 200px;
                width: 200px;
                border-radius: 20px;
                object-fit: cover;
            }
        }


        span {
            color: #4e426d;
            padding: 10px 30px 10px 30px;
        }

        button {
            width: 80%;
            padding: 10px 10px;
            border: none;
            background-color: #4e426d;
            border-radius: 10px;
            color: white;
            font-size: 15px;
            cursor: pointer;

        }
    }
}

// home

.light-theme {
    --msg-bg: url(../images/lightBackground.jpg);
    --component-bg: white;
    --body-bg: #6850a7;
    --app-bg: #e1e1e1;
    --app-text: #4e426d;
    --icon: #4e426d;
    --input-hover: #FAC204;
    --text: black;
    --hover: rgba(223, 223, 223, 0.905);
    --msg: #d1cfd4;
}

.dark-theme {
    --msg: #605f5c;
    --msg-bg: url(../images/darkBackground.jpg);
    --component-bg: #181818;
    --body-bg: #6850a7;
    --app-bg: #0f0f0f;
    --app-text: white;
    --icon: white;
    --input-hover: #004574;
    --text: white;
    --hover: #3f3f3f;
}

