.home {
    color: var(--text);
    height: 100vh;
    width: 100vw;
    // min-width:900px;
    background-color: var(--body-bg);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    font-weight: 400;
    overflow: hidden;

    .homeContainer {
        background-color: var(--app-bg);
        height: 100vh;
        width: 100vw;
        margin: 0;
        box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
        display: flex;

        .sidebar {
            background-color: var(--app-bg);
            height: inherit;
            width: 30%;
            border-radius: 10px 0px 0px 10px;
            // border-right: 1px solid rgb(129, 129, 129);

            .navbar {

                display: flex;
                height: 60px;
                background-color: var(--app-bg);
                padding: 10px;
                // border-bottom: 1px solid rgb(129, 129, 129);
                justify-content: space-between;
                align-items: center;
                color: var(--app-text);
                border-radius: 10px 0 0 0;
                position: relative;

                .appName {
                    display: flex;
                    align-items: center;

                    .logo {
                        height: 70px;
                        margin-left: 20px;
                    }

                    h3 {
                        text-transform: uppercase;
                        font-weight: bolder;

                    }
                }

                .moreOption {
                    button {
                        color: var(--icon);
                        border: none;
                        background-color: transparent;
                        cursor: pointer;
                    }

                }

                .search-pop {
                    color: var(--icon);
                    border: none;
                    background-color: transparent;
                    cursor: pointer;
                    justify-self: end;
                }


                .moreOptions {
                    z-index: 20;
                    display: none;

                    flex-direction: column;
                    position: absolute;
                    top: 55px;
                    color: var(--icon);
                    right: 10px;
                    background-color: var(--component-bg);
                    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
                    border-radius: 10px;

                    .button {
                        display: flex;
                        background-color: var(--component-bg);
                        padding: 10px 15px;
                        border-radius: 10px;

                        button {
                            background-color: transparent;
                            border: none;
                        }

                        &:hover {
                            background-color: var(--hover);
                            cursor: pointer;
                        }
                    }

                }

                .moreOption:hover .moreOptions {
                    display: flex;
                }
            }

            // sidebar components


            //settings
            .settings {
                width: calc(100% - 30px);
                background-color: var(--component-bg);
                display: flex;
                margin: 0 10px 0 20px;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                border-radius: 0 0 30px 30px;
                padding-bottom: 30px;

                .settingsContainer {
                    margin-top: 10px;
                    display: flex;
                    align-items: center;
                    padding: 10px 15px;
                    width: 80%;

                    .theme {
                        width: inherit;

                        color: var(--app-text);

                        p {
                            font-size: 16px;
                            position: relative;
                            display: flex;
                            padding: 10px 0px 0px 0px;
                            align-items: center;
                            color: var(--text);

                            input {
                                position: absolute;
                                right: 0px;
                                height: 20px;
                                width: 20px;

                                &:hover {
                                    cursor: pointer;

                                }
                            }

                            .MuiSvgIcon-root {
                                padding: 5px;
                                color: var(--app-text);
                            }

                        }
                    }

                    .logout {
                        margin-top: 10px;

                        button {
                            display: flex;
                            margin-left: 10px;
                            margin-top: 10px;
                            background-color: rgb(242, 59, 59);
                            border: none;
                            color: white;
                            border-radius: 7px;
                            padding: 7px 12px 7px 12px;
                            font-size: 15px;

                            &:hover {
                                background-color: rgb(216, 38, 38);
                                cursor: pointer;
                                transition: 0.3s ease-in-out;
                            }
                        }

                        p {
                            margin-left: 10px;
                            font-size: 13px;
                            padding: 5px 0 0 0;
                        }
                    }

                    .links {
                        margin-top: 50px;

                        p {
                            font-size: 14px;
                            margin: 0;
                            padding: 0;

                            .MuiSvgIcon-root {
                                font-size: 26px;
                                color: #6539d2;
                            }

                        }
                    }
                }
            }

            // search
            .search {
                width: calc(100% - 30px);
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                background-color: var(--component-bg);
                margin: 0 10px 20px 20px;
                border-radius: 0 0 30px 30px;

                .searchContainer {
                    border-radius: 25px;
                    background-color: var(--app-bg);
                    display: flex;
                    align-items: center;
                    padding: 10px 15px;
                    width: 80%;

                    input {
                        color: var(--text);
                        border: none;
                        width: inherit;
                        background: transparent;
                        outline: none;
                        font-size: 16px;
                        padding: 0 10px;
                    }
                }

                .users {
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    width: 90%;
                    padding: 10px;
                    overflow-y: auto;
                    height: calc(100vh - 246px);


                    .searchedUser {
                        display: flex;
                        width: calc(100% - 10px);
                        margin-bottom: 10px;
                        justify-content: space-between;

                        &:hover {
                            background-color: var(--hover);
                            border-radius: 10px;
                            cursor: pointer;
                        }

                        .user {
                            display: flex;

                            .userImageBlank {
                                height: 50px;
                                width: 50px;
                                background-color: rgb(109, 109, 109);
                                border-radius: 50%;
                                margin: 10px;

                                .userImage {
                                    height: 50px;
                                    width: 50px;
                                    object-fit: cover;
                                    border-radius: 50%;
                                }
                            }

                            .userDetails {
                                display: flex;
                                flex-direction: column;
                                justify-content: center;

                                .status {
                                    font-size: 13px;
                                }
                            }
                        }

                        .addfriend {
                            align-self: center;
                            background-color: transparent;
                            padding: 7px 7px 3px 7px;
                            margin: 5px;
                            color: var(--icon);
                            border: none;
                            border-radius: 50%;

                            &:hover {
                                cursor: pointer;
                                background-color: var(--icon);
                                color: var(--app-bg);
                                transition: ease-in-out 0.2s;
                            }
                        }
                    }
                }
            }

            // chats
            .allChats {

                h3 {

                    background-color: var(--component-bg);
                    color: var(--app-text);
                    font-weight: 500;
                    // padding: 30px 30px 10px 25px;
                    padding: 20px 20px 10px 15px;
                    margin: 10px 10px 0 20px;
                    border-radius: 30px 30px 0 0;
                }

                .allChatsContainer {
                    // box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
                    // background-color: var(--app-bg);
                    background-color: var(--component-bg);
                    overflow-y: auto;
                    z-index: 20;
                    overflow-x: hidden;
                    height: calc(100vh - 166px);
                    margin: 0 10px 20px 20px;
                    border-radius: 0 0 30px 30px;

                    .noUsers {
                        margin-top: 50px;
                        text-align: center;
                    }

                    .chats {
                        margin: 0px 10px 10px 10px;
                        height: 50px;
                        // background-color: var(--);
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        padding: 10px;

                        &:hover {
                            background-color: var(--hover);
                            border-radius: 10px;
                            cursor: pointer;
                        }

                        .users {
                            display: flex;
                            position: relative;

                            .usersImageExpanded {
                                display: none;
                                border-radius: 50%;
                                position: absolute;
                                z-index: 10;
                                top: 50px;
                                left: 50px;

                                img {
                                    height: 200px !important;
                                    width: 200px !important;
                                    object-fit: cover;
                                    background-color: white !important;
                                    border-radius: 10px !important;
                                    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
                                }
                            }

                            .usersImage {
                                height: 50px;
                                width: 50px;
                                background-color: rgb(255, 255, 255);
                                border-radius: 50%;

                                img {
                                    height: 50px;
                                    width: 50px;
                                    object-fit: cover;
                                    border-radius: 50%;
                                }

                                &:hover .usersImageExpanded {
                                    display: block;

                                }
                            }





                            .usersDetails {
                                padding-left: 10px;
                                display: flex;
                                flex-direction: column;
                                justify-content: center;

                                .username {
                                    font-size: 20px;
                                }

                                .message {
                                    overflow-y: hidden;
                                    word-break: break-all;
                                    height: 19px;
                                    opacity: 0.7;
                                    font-size: 14px;
                                }
                            }

                        }

                        .msgStatus {
                            display: flex;
                            flex-direction: column;
                            align-items: flex-end;

                            .time {
                                font-size: 10px;
                            }

                            .msgNo {
                                font-size: 12px;
                                margin-top: 5px;
                                color: var(--text);
                                // background-color: green;
                                // height: 25px;
                                // width: 25px;
                                // border-radius: 50%;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                // font-size: 14px;
                            }
                        }
                    }
                    .chatsLoad {
                        margin: 0px 10px 10px 10px;
                        height: 50px;
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        padding: 10px;
                        width: inherit;
                      

                        .users {
                            display: flex;
                            position: relative;

                            .usersImage {
                                height: 50px;
                                width: 50px;
                                background-color: rgb(124, 123, 123);
                                border-radius: 50%;
                            }
                            .usersDetails {
                                padding-left: 10px;
                                display: flex;
                                flex-direction: column;
                                justify-content: center;

                                .username {
                                    font-size: 20px;
                                    background-color: grey;
                                    height: 20px;
                                    width: 120px;
                                    border-radius: 5px;
                                    animation: myAnim 2s ease 0s infinite normal forwards;
                                    
                                }
                                
                                .message {
                                    border-radius: 5px;
                                    margin-top: 5px;
                                    overflow-y: hidden;
                                    background-color: grey;
                                    height: 15px;
                                    width: 200px;
                                    word-break: break-all;
                                    height: 19px;
                                    opacity: 0.7;
                                    font-size: 14px;
                                    animation: myAnim 2s ease 0s infinite normal forwards;
                                }
                            }

                        }
                        
                        .msgStatus {
                            display: flex;
                            flex-direction: column;
                            align-items: flex-end;
                            
                            .time {
                                background-color: grey;
                                border-radius: 5px;
                                height: 20px;
                                width: 50px;
                                animation: myAnim 2s ease 0s infinite normal forwards;
                            }

                
                        }

                        @keyframes myAnim {
                            0% {
                                opacity: 1;
                            }
                        
                            50% {
                                opacity: 0.2;
                            }
                        
                            100% {
                                opacity: 1;
                            }
                        }
                    }

                }
            }

            .back {
                background-color: var(--component-bg);
                display: flex;
                align-self: flex-start;
                margin: 20px;
                border-radius: 30px 30px 0 0;
                padding: 20px;
                color: var(--app-text);
                margin: 10px 10px 0 20px;
                user-select: none;

                span {
                    cursor: pointer;
                }

                h3 {
                    margin-left: 10px;
                    font-weight: 550;
                    align-self: flex-start;
                    // text-transform: uppercase;
                }
            }

            .profile {
                // width: 100%;
                background-color: var(--component-bg);
                display: flex;
                height: calc(100vh - 224px);
                padding: 20px;
                margin: 0 10px 0 20px;
                align-items: top;
                border-radius: 0 0 30px 30px;
                justify-content: center;
                z-index: -10;
                overflow: auto;
                user-select: none;

                .profileContainer {
                    display: flex;
                    overflow: auto;
                    flex-direction: column;
                    align-items: center;

                    // margin: 50px;



                    .profileImage {
                        margin: 50px;
                        width: 200px;
                        height: 200px;
                        border-radius: 50%;
                        padding: 10px;
                        position: relative;
                        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

                        #userImage {
                            width: 200px;
                            height: 200px;
                            border-radius: 50%;
                            object-fit: cover;
                        }

                        label {
                            position: absolute;
                            right: 0px;
                            bottom: 0;
                            cursor: pointer;
                            color: #004574;
                        }
                    }

                    .input {
                        display: flex;
                        align-items: center;
                        justify-content: flex-start;
                        border-bottom: 2px solid #004574;
                        color: #004574;
                        width: 80%;
                        font-size: 30px;
                        margin: 10px;

                        input {
                            font-size: 16px;
                            padding: 10px;
                            border: none;
                            outline: none;
                            background-color: transparent;
                            color: var(--text);
                        }

                        &:focus-within {
                            border-bottom: 2px solid #FAC204;
                            color: #FAC204;
                        }

                    }

                    .error {
                        margin: 10px 30px;
                        width: 300px;

                    }

                    button {
                        // margin-top: 50px;
                        width: 80%;
                        padding: 10px 10px;
                        border: none;
                        background-color: #004574;
                        border-radius: 10px;
                        color: white;
                        font-size: 15px;
                        cursor: pointer;

                    }

                }
            }
        }

        // chat component
        .chat {
            width: 70%;
            height: inherit;
            border-radius: 0px 10px 10px 0px;

            .selectChat {
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 30px;
                background-color: white;
                background-image: var(--msg-bg) !important;
                margin: 20px 20px 20px 10px;
                height: calc(100vh - 40px);

                .logo {
                    background-color: var(--component-bg);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    border-radius: 20px;
                    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

                    img {
                        min-width: 200px;
                        width: 300px;
                        align-self: center;

                    }

                    .logoName {
                        position: relative;
                        bottom: 60px;
                        font-size: 30px;

                    }

                    p {
                        position: relative;
                        bottom: 40px;
                        text-align: center;
                        font-size: 20px;
                    }
                }
            }

            .userInfo {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 10px;
                margin: 20px 20px 0px 10px;
                background-color: var(--component-bg);
                border-radius: 30px 30px 0 0;
                border-bottom: 1px solid rgb(129, 129, 129);

                .backButton {
                    display: none;
                    align-items: center;
                    margin: 10px;

                    :hover {
                        cursor: pointer;
                    }
                }

                .user {
                    position: relative;
                    display: flex;

                    .usersImageExpanded {
                        display: none;
                        border-radius: 50%;
                        position: absolute;
                        z-index: 10;
                        top: 50px;
                        left: 50px;

                        img {
                            box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
                            height: 300px !important;
                            width: 300px !important;
                            object-fit: cover;
                            background-color: white !important;
                            border-radius: 10px !important;
                        }
                    }

                    .userImage {
                        height: 50px;
                        width: 50px;
                        background-color: rgb(255, 255, 255);
                        border-radius: 50%;

                        img {
                            border-radius: 50%;
                            height: 50px;
                            width: 50px;
                            object-fit: cover;
                        }

                        &:hover .usersImageExpanded {
                            display: block;

                        }

                        &:hover {
                            cursor: pointer;
                        }

                    }


                    .userDetails {
                        padding-left: 10px;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;

                        .chatUsername {
                            font-size: 20px;
                        }

                        .chatStatus {
                            opacity: 0.7;
                            font-size: 14px;
                        }
                    }
                }

                .userInfoOption {
                    button {

                        color: var(--icon);
                        background-color: transparent;
                        border: none;
                        cursor: pointer;
                    }
                }

                .UserInfoOptions {
                    color: var(--icon);
                    display: none;
                    flex-direction: column;
                    position: absolute;
                    top: 70px;
                    right: 40px;
                    background-color: transparent;
                    border-radius: 10px;
                    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;

                    .userDetailsCard {
                        display: flex;
                        background-color: var(--app-bg);
                        border-radius: 10px;
                        padding: 10px 15px;
                        background-color: var(--component-bg);
                        flex-direction: column;
                        z-index: 20;
                        position: relative;

                        padding: 10px;

                        img {
                            height: 300px;
                            width: 300px;
                            object-fit: cover;
                            border-radius: 10px;
                        }

                        .nameStatus {
                            position: absolute;
                            bottom: 10px;
                            left: 0px;
                            width: 100%;
                            display: inline;
                            background-color: var(--component-bg);
                            opacity: 0.4;

                            .name {
                                color: var(--text);
                                font-size: 20px;
                                margin-top: 20px;
                                text-align: center;

                            }

                            .status {
                                text-align: center;
                                margin-bottom: 20px;
                                color: var(--text);
                                font-size: 14px;
                                // opacity: 0.7;
                            }

                        }

                    }

                }

                .userInfoOption:hover .UserInfoOptions {
                    display: flex;
                }
            }

            .message {

                background-image: var(--msg-bg);
                background-color: #0f0f0f;
                display: flex;
                flex-direction: column;
                height: calc(100vh - 174px);
                overflow: auto;
                padding: 0 10px 0px 10px;
                margin: 0px 20px 0px 10px;

                .msg {
                    display: flex;
                    max-width: 80%;
                    width: fit-content;
                    align-items: center;
                    animation: sendmsgAnimation 0.6s ease 0s 1 normal forwards;

                    img {
                        width: 50px;
                        height: 50px;
                        margin: 10px 10px 0 0;
                        object-fit: cover;
                        border-radius: 50%;
                    }

                    .msgd {
                        display: flex;
                        background-color: var(--msg);
                        color: var(--text);
                        border-radius: 0 10px 10px 10px;
                        margin: 10px 0 5px 0px;

                        .msgText {
                            word-break: break-all;
                            padding: 10px 0 10px 10px;
                        }

                        .msgTime {
                            font-size: 11px;
                            align-self: end;
                            padding: 6px;
                            opacity: 0.8;
                        }
                    }
                }



                .sendMsg {
                    display: flex;
                    flex-direction: row-reverse;
                    position: relative;
                    max-width: 80%;
                    width: fit-content;
                    align-items: center;
                    align-self: end;
                    animation: sendmsgAnimation 0.6s ease 0s 1 normal forwards;

                    img {
                        width: 50px;
                        height: 50px;
                        margin: 10px 0 0 10px;
                        object-fit: cover;
                        border-radius: 50%;
                    }

                    .msgd {
                        display: flex;

                        margin: 10px 0 5px 0px;
                        color: white;
                        background-color: var(--body-bg);
                        border-radius: 10px 0 10px 10px;

                        .msgText {
                            padding: 10px 0 10px 10px;
                            word-break: break-all;

                        }

                        .msgTime {
                            font-size: 11px;
                            align-self: end;
                            padding: 6px;
                            opacity: 0.8;
                        }
                    }
                }
            }

            @keyframes sendmsgAnimation {
                0% {
                    transform: scale(0);
                }

                100% {
                    transform: scale(1);
                }
            }


            .input {
                background-color: var(--component-bg);
                border-radius: 0 0 30px 30px;
                height: 60px;
                width: calc(100% - 30px);
                margin: 0px 20px 10px 10px;

                border-top: 1px solid rgb(129, 129, 129);
                display: flex;
                align-items: center;
                justify-content: center;

                .inputContainer {
                    border-radius: 30px;
                    background-color: var(--app-bg);
                    display: flex;
                    align-items: center;
                    padding: 7.5px;
                    width: calc(100% - 30px);
                    // margin: 0px 10px 10px 10px;
                    // margin: 10px;

                    input {
                        color: var(--text);
                        border: none;
                        width: inherit;
                        background: transparent;
                        outline: none;
                        font-size: 16px;
                        padding: 0 10px;

                    }

                    button,
                    label {
                        outline: none;
                        border: none;
                        background-color: transparent;
                        color: #3ba68b;

                        :hover {
                            color: #FAC204;
                            cursor: pointer;
                            transition: ease-in 0.1s;
                        }
                    }


                }
            }
        }

    }

    @media only screen and (max-width: 850px) {

        .sidebar {
            width: 100vw !important;
            margin: 0 10px 0 0;
            display: block;
            animation: sidebar 0.1s ease 0s 1 normal forwards;

            @keyframes sidebar {
                0% {
                    transform: translateX(-100vw);
                }

                100% {
                    transform: translateX(0);
                }
            }

        }

        .chat {
            // margin: 0 0 0 10px;
            overflow: hidden;
            width: 100% !important;
            background-image: var(--msg-bg) !important;
            display: none;
            animation: chat 0.2s ease 0s 1 normal forwards;

            @keyframes chat {
                0% {
                    transform: translateX(-100vw);
                }

                100% {
                    transform: translateX(0);
                }
            }


            .userInfo {

                margin: 0px !important;
                border-radius: 0 0 20px 20px !important;
                border-bottom: none !important;

                .backButton {
                    display: flex !important;
                }

                .userInfoOption {
                    .UserInfoOptions {
                        width: 100% !important;
                        top: 100px !important;
                        right: 0px !important;
                        // display: flex !important;
                        align-items: center;
                        justify-content: center;
                        box-shadow: none !important;

                        .userDetailsCard {
                            box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

                        }
                    }
                }


            }

            .message {
                margin: 0px !important;
                background-color: var(--app-bg) !important;
                height: calc(100vh - 141px) !important;
                padding: 5px 10px 5px 10px !important;

            }

            .input {
                margin: 0 !important;
                width: 100% !important;
                border-top: none !important;
                border-radius: 20px 20px 0 0 !important;

                .inputContainer {
                    border-radius: 20px !important;
                }
            }
        }
    }
}